.item {
    margin: 5px;
    padding: 10px;
    width: 90px;
    height:90px;
    align-items: center;
    justify-content: center;
    clip-path:circle(40%);
    border-radius: 50%;
    /* background-color:rgb(9, 201, 153); */
    /* background-color: #e52e71; */
    background-color:rgb(179, 9, 201);
    /* background-image: linear-gradient(0deg,#ffd900,#e52e71); */
    justify-items: center; 
    text-align: center; 
    color:white;
    font-size: 13pt;
    font-family: 'Roboto slab';
    transition: all 0.5s ease-in;
    border: 2px solid white;
   

     
    }


.item a {
    text-decoration:none;
    color:white;
    
    
}

.item  a:hover {
    font-weight: 700;
    
}

.item:hover {

    background: #00B6FF;
    /* background-color: #ffd900; */
    clip-path:circle(100%);
    transition: all 0.5s ease-out;
    font-size: 26pt;
    width: 180px;
    height:180px;
    padding-top:8%;
   
    border-radius: 50%;
    /* font-size: 36pt; */
    /* width: 250px;
    height:250px; */
    justify-items: center;  
    cursor: pointer;
    font-family: 'Roboto slab';
    border: 2px solid white;
    z-index: 4;
        
    /* background-image: linear-gradient(0deg,#e52e71,#ffd900); */
   

}

.item:hover > .inner{
    z-index: 2;
}





  
    


