/*  falling leaves animation css*/
.leaves-container
{
    position: absolute;
    width: 100%;
    height: 100%;
}

.leaves-container > img{
    width: 75px;
    height: 75px;
    position: absolute;

}

.leaves-container > img:nth-child(1) {
    left:30px;
    animation:animate 7s linear infinite;
}

.leaves-container > img:nth-child(2) {
    left:120px;
    animation:animate 4s linear infinite;
}

.leaves-container > img:nth-child(3) {
    left:210px;
    animation:animate 5s linear infinite;
}

.leaves-container > img:nth-child(4) {
    left:1280px;
    animation:animate 7s linear infinite;
}

.leaves-container > img:nth-child(5) {
    left:1380px;
    animation:animate 5s linear infinite;
}

.leaves-container > img:nth-child(6) {
    left:1460px;
    animation:animate 9s linear infinite;
}

@keyframes animate {
    0% {
        top:-20%;
        opacity:0.2;
        transform:translate(-20px, 20px) rotate(0deg);
    }
